@use "../abstracts";

/* toggle css */
.components-form-toggle {
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  -webkit-box-shadow: 1px 1px 1px 0 #00000024;
  align-items: center;
  background: abstracts.$white-theme-color;
  border: 1px solid #4d80ab;
  box-shadow: 1px 1px 1px 0 #00000024;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 30px;
  justify-content: center;
  padding: 0 0;
  width: 70px;

  span {
    display: none !important;
  }

  input {
    margin: 0 !important;
    right: 0;
    width: 50px !important;
  }

  &::after {
    -ms-flex-align: center;
    -ms-flex-pack: center;
    -o-transition: 0.5s all;
    -webkit-box-align: center;
    -webkit-box-pack: center;
    -webkit-transition: 0.5s all;
    align-items: center;
    border: 0 !important;
    color: abstracts.$primary-theme-color !important;
    color: abstracts.$white-theme-color;
    content: "ON";
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 11px;
    height: 100%;
    justify-content: center;
    position: absolute;
    right: 0;
    top: 0;
    transition: 0.5s all;
    width: 50%;
    z-index: 9;
  }

  &::before {
    -ms-flex-align: center;
    -ms-flex-pack: center;
    -o-transition: 0.5s all;
    -webkit-box-align: center;
    -webkit-box-pack: center;
    -webkit-transition: 0.5s all;
    align-items: center;
    border: 0 !important;
    color: abstracts.$white-theme-color !important;
    content: "OFF";
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 11px;
    height: 100%;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    transition: 0.5s all;
    width: 50%;
    z-index: 9;
  }

  svg {
    display: none;
  }

  input[type="checkbox"] {
    height: 30px !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 70px !important;
    z-index: 99 !important;
  }

  span.components-form-toggle__thumb {
    -ms-transform: translate(0, 0);
    -ms-transform-origin: 0% 0%;
    -o-transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    -webkit-font-smoothing: antialiased;
    -webkit-transform: translate(0, 0);
    -webkit-transform-origin: 0% 0%;
    -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    background: #4d80ab !important;
    border: 0 !important;
    border-radius: 0 !important;
    bottom: 0;
    display: block !important;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    transform: translate(0, 0);
    transform-origin: 0% 0%;
    transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition:
      transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
      -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    width: 50%;
    z-index: 0;
  }
}

.components-form-toggle.is-checked {
  &::before {
    background: none;
    color: abstracts.$primary-theme-color !important;
  }

  &::after {
    color: abstracts.$white-theme-color !important;
  }

  span.components-form-toggle__thumb {
    -ms-transform: translate(100%, 0);
    -ms-transform-origin: 100% 0%;
    -o-transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    -webkit-font-smoothing: antialiased;
    -webkit-transform: translate(100%, 0);
    -webkit-transform-origin: 100% 0%;
    -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transform: translate(100%, 0);
    transform-origin: 100% 0%;
    transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition:
      transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
      -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  }
}

.inspector-field.inspector-field-Numberofitems + .components-toggle-control {
  label {
    padding-right: 70px;
  }
}

// Alignments

.alignment-settings {
