    &:last-child {
      margin-right: 0;
    }

    p {
      margin-bottom: 18px;
    }

    .components-toolbar.components-toolbar {
      > div + div {
        margin-left: 0;
      }
    }
  }
}

.components-panel__row {
  .components-base-control {
    .components-base-control__field {
      input.components-range-control__number {
