      }
    }
  }
}

/* Inspector Control CSS */
.inspector-field {
  margin-bottom: 5px;
  position: relative;
  width: 100%;

  > label {
    display: block;
    line-height: 18px;
    margin-bottom: 10px;
    width: 100%;

    &::after {
      clear: both;
      content: "";
      display: block;
    }

    .inspector-device {
      display: inline-block;
      margin-left: 5px;

      > span {
        padding: 0 2.5px;
      }
    }
  }

  button.components-color-palette__clear {
    -webkit-box-shadow: none !important;
    background: transparent !important;
    border: 0;
    box-shadow: none !important;
    color: #8d96a0 !important;
    font-family: "FontAwesome", sans-serif;
    font-size: 0 !important;

    &::after {
      content: "\f0e2";
      font-size: 16px;
    }
  }

  .components-range-control {
    .components-range-control__slider {
      -webkit-appearance: none;
      background: transparent;
      border: 0;
      border-radius: 3px !important;
      height: 35px !important;
      padding-left: 0;
      padding-right: 8px;
      width: 100%;
    }

    .components-range-control__slider + input.components-range-control__number {
      border: 1px solid #e2e4e7;
    }
  }

  .components-panel__row {
    margin-top: 15px;
  }

  .col-main-4 {
    display: flex;
    justify-content: space-between;

    .col-main-inner {
      width: 22%;

      label {
        display: block;
        font-size: 12px;
        margin: 0 auto;
        width: 100%;
      }
    }
  }
}

.inspector-field-button-list-fluid {
  display: flex;
  justify-content: space-between;

  .inspector-button {
    justify-content: center;
    width: 100%;
  }
}

.inspector-button {
  align-items: center;
  background: abstracts.$white-theme-color;
  border: 1px solid #e5e7ea;
  color: #8d96a0;
  cursor: pointer;
  display: inline-flex;
  min-height: 26px;
  padding: 3px 8px;
  text-align: center;
}

.inspector-field-button-list {
  .inspector-button {
    display: flex;

    &:not(:last-child) {
      border-right: 0;
    }

    &:first-child {
      border-radius: 3px 0 0 3px;
    }

    &:last-child {
      border-radius: 0 3px 3px 0;
    }
  }

  .inspector-field-type-none {
    line-height: 1;
    margin-top: 1px;

    svg {
      width: 15px;
      height: 15px;
      transform: rotate(85deg);
    }
  }
}

.inspector-button.active {
  background: #0f6cb6;
  color: abstracts.$white-theme-color;

  svg {
    fill: abstracts.$white-theme-color !important;
    stroke: abstracts.$white-theme-color !important;

    * {
      fill: abstracts.$white-theme-color !important;
      stroke: abstracts.$white-theme-color !important;
    }
  }

  span {
    border-color: abstracts.$white-theme-color;
  }
}

.inspector-field-color {
  button.components-button.is-link {
    background-image: -o-linear-gradient(
      355.7deg,
      rgb(104, 213, 253) 0.7%,
      rgb(137, 255, 0) 18.6%,
      rgb(255, 242, 0) 39.1%,
      rgb(255, 83, 0) 63%,
      rgb(255, 0, 138) 85.3%,
      rgb(0, 139, 255) 108.2%
    );
    background-image: linear-gradient(
      94.3deg,
      rgb(104, 213, 253) 0.7%,
      rgb(137, 255, 0) 18.6%,
      rgb(255, 242, 0) 39.1%,
      rgb(255, 83, 0) 63%,
      rgb(255, 0, 138) 85.3%,
      rgb(0, 139, 255) 108.2%
    );
    border-radius: 50%;
    font-size: 0;
    height: 32px;
    width: 32px;
  }

  .components-color-palette__custom-clear-wrapper {
    align-items: center;
    width: 100%;
  }
}

.inspector-field-transform {
  button.inspector-button {
    span {
      display: inline-block;
      margin-top: -2px;
    }
  }
}

.inspector-svg-fill {
  fill: #7b8086;
}

.inspector-svg-stroke {
  stroke: #8d96a0;
}

.inspector-field-border-type {
  display: block;
  height: 0;
  width: 16px;
}

.inspector-field-border-type.inspector-field-border-type-solid {
  border-top: 3px solid #8d96a0;
}

.inspector-field-border-type.inspector-field-border-type-dotted {
  border-top: 3px dotted #8d96a0;
}

.inspector-field-border-type.inspector-field-border-type-dashed {
  border-top: 3px dashed #8d96a0;
}

.inspector-field-border-type.inspector-field-border-type-double {
  border-top: 6px double #8d96a0;
}

.inspector-field-border-type.inspector-field-border-type-none {
  height: auto;
}

.inspector-bottom-20 {
  margin-bottom: 20px;
}

.bg-img-setting {
  button.components-button.image-button {
    height: auto;
    margin-bottom: 15px;
    max-width: 100%;
    padding: 0;
  }
}

.components-toggle-control {
  .components-base-control__field {
    min-height: 30px;
