}

.hide-time {
  padding-bottom: 5px;

  .components-datetime__time-field-time {
    display: none;
  }
}

.components-base-control {
  select {
    height: 40px;
    max-width: 100% !important;
    min-width: 100%;
    width: 100% !important;
  }
}

/* Tooltip */
.has-tooltip {
  position: relative;

  &:hover {
    &::after {
      -ms-transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      background: #32373c;
      border-radius: 4px;
      bottom: calc(100% + 10px);
      color: abstracts.$white-theme-color;
      content: attr(data-tooltip);
      font-size: 11px;
      height: auto;
      left: 50%;
      line-height: normal;
      max-height: 120px;
      overflow: auto;
      padding: 5px 3px;
      position: absolute;
      transform: translateX(-50%);
      white-space: normal;
      width: calc(100% + 30px);
      z-index: 9;
    }

    &::before {
      -ms-transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid #32373c;
      bottom: 100%;
      content: "";
      height: 0;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      width: 0;
    }
  }
}

.padding-setting {
  div[class*="padding-"] {
    float: left;
    margin-bottom: 10px;
    margin-right: 5%;
    padding-left: 23px;
    position: relative;
    width: 47.5%;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .components-base-control {
    margin-bottom: 10px;
  }
}

.margin-setting {
  div[class*="margin-"] {
    float: left;
    margin-bottom: 10px;
    margin-right: 5%;
    padding-left: 23px;
    position: relative;
    width: 47.5%;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .components-base-control {
    margin-bottom: 5px;
  }
}

.md_slider_section__items {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin: 40px 0 0;
  .md_slider_section__item {
    width: 100px;
    height: 100px;
    border: 1px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &.active {
      background: #000;
      color: #fff;
      font-weight: 700;
    }
  }
}

.md_slider_grid {
  .md_slider_grid_item {
    display: none;
    &.active {
      display: block;
    }
  }
}

.md-btn-main {
  font-weight: 500;
  font-size: 1rem;
  padding: 0.375rem 0.75rem;
  background-color: #000;
  color: #fff;
  border: 1px solid #000;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
  display: inline-block;
  a {
    color: inherit;
    text-decoration: none;
  }
  &:hover {
    background-color: #fff;
    color: #000;
  }
  &.btn-large {
    font-size: 15px;
    padding: 14px 25px;
  }
  &.btn-extra-large {
    font-size: 17px;
    padding: 16px 30px;
  }
  &.has-right-arrow {
	display: flex;
	flex-direction: row;
	gap: 5px;
	align-items: center;
	&:hover {
		flex-direction: row-reverse;
	}
  }
}
